@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Option {
  height: 10vh;
  width: 40vw;
  margin: auto;
  background-color: #282c34;
  border-radius: 5px;
  font-size: 2.5em;
  font-family: "Noto Sans", sans-serif;
  display: flex;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Option-id {
  height: 10vh;
  width: 5vw;
  background-color: #18b0b0;
  color: #282c34;
  border-radius: 5px 0 0 5px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.Option-value {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 768px) {
  .Option {
    height: 8vh;
    width: 90vw;
    font-size: 2em;
  }

  .Option-id {
    height: 8vh;
    width: 16vw;
  }
}

.OptionsContainer {
  height: 40vh;
  width: 100vw;
  background-color: #363b46;

  display: flex;
  flex-wrap: wrap;

  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 768px) {
  .OptionsContainer {
    height: 50vh;
  }
}

.ScoreBoard {
  position: absolute;
  top: 2.5vh;
  background: #363b46;
  padding: 0.5em;
  border-radius: 0.5em;
  font-size: 14px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  letter-spacing: 1px;
}

.ScoreBoard-item {
  margin: 0em 2em;
}

.App {
  text-align: center;
}

.App-play-button,
.App-pause-button {
  height: 128px;
  width: 128px;
  cursor: pointer;
}

.App-loading-icon {
  height: 200px;
  width: 200px;
}

.App-alpha-container {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App-alpha {
  font-size: 8em;
  font-family: "Baloo Tamma 2", sans-serif;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

@media only screen and (max-width: 768px) {
  .App-alpha-container {
    height: 50vh;
  }
}

